    <div class="game d-flex justify-center align-center">
      <!-- Âè≥ÂÅ¥Ê¨Ñ -->
      <div class="gameSetFrame">
        <v-icon class="gameSet" @click.stop="dialogSet = true">mdi-cog-outline</v-icon>
        <v-icon class="gameSet mt-2">mdi-history</v-icon>
      </div>
      <!-- Ë®≠ÂÆöÂ∞çË©±Ê°Ü -->
      <v-dialog
        v-model="dialogSet"
        max-width="400"
        persistent
        scrollable
      >
        <v-card>
          <!-- Ê®ôÈ°å -->
          <v-card-title class="d-flex justify-center font-weight-bold orange--text">
            ÊäΩÁçéË®≠ÂÆö
          </v-card-title>
          <!-- ÊñáÂ≠ó„ÄÅÈÅ∏È†Ö -->
          <vue-scroll>
            <v-card-text class="d-flex justify-center align-center flex-column pa-0">
              <!-- ÈÅ∏ÊìáÊñπÂºè -->
              <v-radio-group v-model="radioSet">
                <v-radio
                  label="ÂèØÈáçË§áÊäΩ‰∏≠Âêå‰∏ÄÁ¥ôÊ¢ù"
                  value="w1"
                  color="dhblue"
                ></v-radio>
                <v-radio
                  label="‰∏çÈáçË§á"
                  value="w2"
                  color="dhblue"
                ></v-radio>
              </v-radio-group>
              <!-- TODO ‰ª•ÂæåÊúâÊ©üÊúÉÂÜçÂÅöÂàÜ ÁçéÂìÅ Âíå ÂêçÂñÆ È†ÖÁõÆ -->
              <!--È†ÖÁõÆÂÖßÂÆπ -->
              <v-text-field
                class="paper"
                v-for="(input,idx) in inputs"
                :key="idx"
                :label="input.num"
                v-model="input.item"
                color="success"
                style="width:60%;"
              >
                <!-- Á¥ôÊ¢ù > 3 ÊúÉÂá∫ÁèæÂèØÂà™Èô§ icon -->
                <template
                  v-if="inputs.length >= 3"
                  v-slot:append-outer
                >
                  <v-icon
                    class="inputDel"
                    @click="deletInput(idx)"
                  >
                    mdi-delete-forever
                  </v-icon>
                </template>
              </v-text-field>
              <!-- Â¢ûÂä† -->
              <v-icon
                class="inputAdd"
                @click="addInput"
              >
                mdi-plus-circle-outline
              </v-icon>
            </v-card-text>
          </vue-scroll>
          <!-- button -->
          <v-card-actions class="d-flex justify-center pb-4">
            <!-- ÂèØÂ∞çÊáâÂà∞ÊñáÂ≠ó -->
            <!-- <span>{{inputs[0].item}}</span> -->
            <v-btn
              color="green darken-1"
              style="height:30px;font-size:0.95rem;"
              text
              @click="submitInput"
            >
              Á¢∫ÂÆö
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

      <!-- ÁÆ±Â≠ê class shake-constant shake-hard -->
      <!-- TODO ÊäΩÂá∫‰æÜÊôÇÔºåÂÅöÊúâÊâãÈÅÆ90%ÊïàÊûúÔºåË£ΩÈÄ†Âà∫ÊøÄÊÑü(ÊúâÊôÇÈñìÂ∞±ÂÅö) or ÂÉèÈ≠îË°ì‰∏ÄÊ®£Áî®Âê∏ÁöÑ‰∏ä‰æÜ -->
      <div id="box" ref="box" class="d-flex justify-center">
        <!-- ÁõíÂ≠êÊ°Ü -->
        <div id="border">
        <!-- TODO Á¥ôÊ¢ùÔºöÈñãÂßãÂæåÂÅöÁøªÈù¢ÊïàÊûúÔºåÂ•ΩÂæåÂÅö shake + Á¥ôÊ¢ùÈ£ÑÁßª + (Á¥ôÁÆ±ËÆäËâ≤) -->
        <div
          v-for="(input,idx) in inputs"
          :key="idx"
          class="paper"
          :style="input.position"
        >
          <span class="text">{{input.item}}</span>
        </div>
        </div>
      </div>
      <!-- ÈñãÂßãÈçµ -->
      <div
        class="startBig d-flex justify-center align-center"
        ref="startBig"
        @click="start($event)"
      >
        GO
      </div>
      <!-- ÈÅÆÊâã -->
      <span class="maskHand">ü§ö</span>
    </div>
  </div>
